import React from 'react';
import CarouselItem from 'react-bootstrap/CarouselItem';
import Carousel from 'react-bootstrap/Carousel';

export default function CarouselSlide({ images }) {

    return (
        <Carousel  nextIcon={null} prevIcon={null} style={{ width: "50%" }}>
            {images.map((x, i) =>
                <CarouselItem key={`slide-image-${i}`} >
                    <div style={{display:"flex", width:"100%", flexDirection:"column", alignItems:"center"}}>
                    <img width="700px" height="700px" src={x.url} alt={x.title} />
                    
                    <Carousel.Caption style={{position:"relative", left:"0"}}>
                        <h2 style={{color:"black"}}>{x.title}</h2>
                        <p style={{color:"black"}}><em>{x.desc}</em></p>

                    </Carousel.Caption>
                    </div>
                </CarouselItem>
            )}
        </Carousel>
    )

}